  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
}
.sxa-form .form-header {
  margin-bottom: 35px;
  text-align: center;
}
.sxa-form .form-header .form-title {
  margin: 0 0 10px 0;
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
}
.sxa-form .form-header .form-description {
  margin: 0;
  color: var(--text-secondary);
  font-size: 15px;
}
.sxa-form .contact-form .form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
@media (max-width: 576px) {
  .sxa-form .contact-form .form-row {
    grid-template-columns: 1fr;
  }
}
.sxa-form .contact-form .form-group {
  margin-bottom: 25px;
}
.sxa-form .contact-form .form-group .form-label {
  display: block;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}
.sxa-form .contact-form .form-group .form-control {
  width: 100%;
  padding: 12px 16px;
  background: var(--bg-body);
  border: 1px solid var(--border-color);
  border-radius: 0.25rem;
  font-size: 15px;
  color: var(--text-primary);
  transition: all 0.15s ease-in-out;
}
.sxa-form .contact-form .form-group .form-control:focus {
  outline: none;
  border-color: var(--color-primary);
  box-shadow: 0 0 0 3px rgba(0, 120, 212, 0.1);
}
.sxa-form .contact-form .form-group .form-control.error {
  border-color: var(--color-danger);
}
.sxa-form .contact-form .form-group .form-control.error:focus {
  box-shadow: 0 0 0 3px rgba(220, 53, 69, 0.1);
}
.sxa-form .contact-form .form-group .form-control.success {
  border-color: var(--color-success);
}
.sxa-form .contact-form .form-group textarea.form-control {
  resize: vertical;
  min-height: 120px;
}
.sxa-form .contact-form .form-group .form-error {
  display: block;
  margin-top: 6px;
  font-size: 13px;
  color: var(--color-danger);
  min-height: 18px;
}
.sxa-form .contact-form .form-check {
  display: flex;
  align-items: flex-start;
  gap: 10px;
}
.sxa-form .contact-form .form-check .form-check-input {
  margin-top: 4px;
  width: 18px;
  height: 18px;
  cursor: pointer;
}
.sxa-form .contact-form .form-check .form-check-label {
  font-size: 14px;
  color: var(--text-secondary);
  cursor: pointer;
}
.sxa-form .contact-form .form-actions {
  display: flex;
  gap: 15px;
  margin-top: 30px;
}
@media (max-width: 576px) {
  .sxa-form .contact-form .form-actions {
    flex-direction: column;
  }
}
.sxa-form .contact-form .form-actions .btn {
  padding: 14px 32px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 0.375rem;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.sxa-form .contact-form .form-actions .btn.btn-primary {
  background: var(--color-primary);
  color: white;
  border: none;
}
.sxa-form .contact-form .form-actions .btn.btn-primary:hover {
  background: var(--color-primary-dark);
  transform: translateY(-2px);
  box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
}
.sxa-form .contact-form .form-actions .btn.btn-primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}
.sxa-form .contact-form .form-actions .btn.btn-secondary {
  background: transparent;
  color: var(--text-secondary);
  border: 1px solid var(--border-color);
}
.sxa-form .contact-form .form-actions .btn.btn-secondary:hover {
  background: var(--bg-surface);
  border-color: var(--text-secondary);
}
.sxa-form .form-success {
  text-align: center;
  padding: 60px 20px;
}
.sxa-form .form-success .success-icon {
  width: 80px;
  height: 80px;
  margin: 0 auto 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--color-success);
  color: white;
  font-size: 48px;
  border-radius: 50%;
  animation: scaleIn 0.5s ease-out;
}
.sxa-form .form-success h3 {
  margin: 0 0 15px 0;
  font-size: 1.75rem;
  color: var(--text-primary);
}
.sxa-form .form-success p {
  margin: 0;
  color: var(--text-secondary);
  font-size: 16px;
}
.sxa-form[data-variant=newsletter] .form-container {
  max-width: 500px;
  padding: 30px;
}
.sxa-form[data-variant=newsletter] .contact-form {
  display: flex;
  gap: 10px;
}
.sxa-form[data-variant=newsletter] .contact-form .form-group {
  flex-grow: 1;
  margin-bottom: 0;
}
.sxa-form[data-variant=newsletter] .contact-form .form-actions {
  margin-top: 0;
}

@keyframes scaleIn {
