n                    border-color: var(--color-danger);\r\n\r\n                    &:focus {\r\n                        box-shadow: 0 0 0 3px rgba(220, 53, 69, 0.1);\r\n                    }\r\n                }\r\n\r\n                &.success {\r\n                    border-color: var(--color-success);\r\n                }\r\n            }\r\n\r\n            textarea.form-control {\r\n                resize: vertical;\r\n                min-height: 120px;\r\n            }\r\n\r\n            .form-error {\r\n                display: block;\r\n                margin-top: 6px;\r\n                font-size: 13px;\r\n                color: var(--color-danger);\r\n                min-height: 18px;\r\n            }\r\n        }\r\n\r\n        .form-check {\r\n            display: flex;\r\n            align-items: flex-start;\r\n            gap: 10px;\r\n\r\n            .form-check-input {\r\n                margin-top: 4px;\r\n                width: 18px;\r\n                height: 18px;\r\n                cursor: pointer;\r\n            }\r\n\r\n            .form-check-label {\r\n                font-size: 14px;\r\n                color: var(--text-secondary);\r\n                cursor: pointer;\r\n            }\r\n        }\r\n\r\n        .form-actions {\r\n            display: flex;\r\n            gap: 15px;\r\n            margin-top: 30px;\r\n\r\n            @media (max-width: $breakpoint-sm) {\r\n                flex-direction: column;\r\n            }\r\n\r\n            .btn {\r\n                padding: 14px 32px;\r\n                font-size: 16px;\r\n                font-weight: $font-weight-semibold;\r\n                border-radius: $border-radius;\r\n                transition: $transition-base;\r\n                cursor: pointer;\r\n\r\n                &.btn-primary {\r\n                    background: var(--color-primary);\r\n                    color: white;\r\n                    border: none;\r\n\r\n                    &:hover {\r\n                      